.container {
  width: 100%;
  height: 100%;
  padding: 10px 8px;
  box-sizing: border-box;
  color: #333;
}
.container .top-item {
  display: flex;
  width: 100%;
  height: 100px;
  border-radius: 5px;
  overflow: hidden;
  background: #fff;
  margin-bottom: 10px;
  box-shadow: 0 2px 2px #c8c8c8;
}
.container .top-item img {
  width: 100px;
  height: 100px;
}
.container .top-item .top-info {
  flex-grow: 1;
  margin-left: 15px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.container .top-item .top-info div {
  width: 80%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.container .top-item .top-info .name {
  font-size: 18px;
  font-weight: 500;
  margin-bottom: 5px;
}
.container .top-item .top-info p {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  font-size: 12px;
  line-height: 1.5;
  color: #868686;
}
